import Layout from '../components/Layout.js'
import Link from 'next/link'
import Head from 'next/head';
import ReactMarkdown from 'react-markdown'

const getTitle = (data, query) => {
  var post = null
  if (query.id != 'favicon.ico') post = data.find(item => { return item.slug == query.id })
  return post != null ? post.title : ''
}

const Post = (props) => (
  <div>
    <Head>
      <title>{props.title} | React patterns and techniques to use in development for React Developers</title>
      <meta name="viewport" content="width=device-width, initial-scale=1, shrink-to-fit=no" />
      <link rel="shortcut icon" href="../static/favicon.ico" />
      <meta name="description" content={props.title} />
      <meta name="author" content="Bunlong" />
      <meta property="og:type"            content="website" /> 
      <meta property="og:url"             content="https://reactpatterns.github.io" /> 
      <meta property="og:title"           content={props.title} /> 
      <meta property="og:image"           content="https://reactpatterns.github.io/static/reactjs.png" /> 
      <meta property="og:description"     content={props.title} />
      <meta name="twitter:card" content="summary" />
      <meta name="twitter:site" content="@reactjspatterns" />
      <meta name="twitter:title" content={props.title} />
      <meta name="twitter:description" content={props.title} />
      <meta name="twitter:image" content="https://reactpatterns.github.io/static/reactjs.png" />
      <link href='../static/styles.css' rel='stylesheet' />
    </Head>
    <Layout>
      <ReactMarkdown source={props.content} />
    </Layout>
  </div>
)

Post.getInitialProps = async function({ query }) {
  var content = null
  const data = await require('../docs/posts.json')
  if (query.id != 'favicon.ico') content = await require(`../docs/${query.id}.md`)
  return { content, title: getTitle(data, query) }
}

export default Post


